import React, { useState, useEffect } from 'react';
import axios from 'axios'
import key from './key'
import styled from 'styled-components'
import Main from './components/Main/Main'
import Spinner from './components/Spinner/Spinner'


const App = () => {
	
	const [data, setData] = useState(false)
	const [spinner, setSpinner] = useState(false)

	useEffect(()=>{
		if(!data) return
		else{
			console.log(data.Title)
		}
		}
	,[data])
	
	const Wrapper = styled.div`
		max-width: 100vw;
		display: flex;
		flex-direction: column;
		input{
			
			padding: 5px;
		}	
	`
	const Input = styled.input`
		margin: auto;
		width: 50%;
		margin-bottom: 20px;
		margin-top: 20px;	
	`
	
	const fetch = () => {
		setSpinner(true)
		const id = Math.floor(Math.random()*(99-10)+10)
		axios.get(`http://www.omdbapi.com/?apikey=${key}&i=tt01206${id}`)
			.then(res => setData(res.data))
			.catch(err => console.log(err))
			.then(setTimeout(()=>setSpinner(false),1000))
		}
	

	// const movies = search ? 
		
	// 		data.slice(0,1).map(el=>{
	// 			return <div style={{
	// 				display: 'flex'
	// 			}}>
	// 				<p>title: {el.Title} ({el.Year})</p>
	// 			</div>
	// 		})
		
	// 		: 
	// 		<p>type something</p>
	//console.log(search ? search[0].Poster : false);
	return (
		<Wrapper>	
			<div style={{display: 'flex'}}>
			<button style={{width: '200px', margin: '20px'}} onClick={fetch}>click me</button>
			<Input placeholder="Search a movie"/>
			</div>

			{spinner ? <Spinner /> :	<Main
				title={data.Title}
				plot={data.Plot}
				img={data.Poster}
				released={data.Released}
				genre={data.Genre}/>}
		</Wrapper>
	);

}

export default App;
