import React from 'react'
import styled from 'styled-components'

const main = (props) => {

	const Wrapper = styled.div`
		// border: 1px solid black;
		margin: auto;
		display: flex;
		width: 90vw;
		flex-direction: column;
		flex-wrap: wrap;
		background: rgba(0,0,0,.5);
		@media (min-width: 500px){
			width: 400px;
		}
		@media (min-width: 600px){
			width: 500px;
		}
		@media (min-width: 700px){
			flex-direction: row-reverse;
			flex-wrap: nowrap;
			width: 600px;

			
		}
		@media (min-width: 950px){
			width: 900px;
		}
	`

	const SectionInfo = styled.section`
		display: flex;
		flex-direction: column;
		padding: 0 20px;
		@media (min-width: 500px){
			width: 100%;
		}
		@media (min-width: 700px){
			min-width: 66.66%;
		}
		`
		const SectionPhoto = styled.section`
		// border: 1px solid red;
		margin: auto;
		min-width: 100%;
		img{
			border-radius: 3px;
			width: 100%;
		}
		@media (min-width: 500px){
			max-width: 400px;
		}
		@media (min-width: 700px){
			min-width: 33.33%;
			// border: 1px solid green;
			margin: 0;	
			
		}		
	`
	const H1Tag = styled.h1`
			
		font-size: 2.3em;
		color: #f5f5f5;
		flex-basis: 20%;
	`
	const PlotParagraph = styled.p`
		
		color: #eeeeee;
	`;

	const ImgTag = styled.img`
		padding: 0 7%;
	`;
	const GenreTag = styled.span`
		color: #00e676;	
	`;
	const { title, plot, img, released, genre } = props

	const Title = !title ? 'Title' : title;
	const Plot = !plot ? 'Officia consectetur nostrud occaecat est eiusmod. Do Lorem elit sunt tempor enim cillum. Nostrud reprehenderit velit consequat occaecat excepteur ullamco. Sit occaecat consectetur aliquip incididunt dolore et eiusmod nisi ad dolore nulla.' : plot
	const Img = !img ? "https://images-na.ssl-images-amazon.com/images/M/MV5BMTYzOTE1OTU5Ml5BMl5BanBnXkFtZTcwNTgzMjUyMQ@@._V1_SX300.jpg" : img
	const Released = !released ? "01 Jan 2000" : released
	const Genre = !genre ? "Fantasy" : genre
	return (
		<Wrapper>
			<SectionInfo>
				<H1Tag>{Title}</H1Tag>
				<PlotParagraph>{Plot}</PlotParagraph>
				<span>{Released}</span>
				<GenreTag>{Genre}</GenreTag>
			</SectionInfo>
			<SectionPhoto>
				<ImgTag src={Img} alt="poster"/>
			</SectionPhoto>

		</Wrapper>
	)
}

export default main